<template>
  <view class="custom-tab-bar">
    <view
      v-for="(item, index) in tabList"
      :key="index"
      class="tab-item"
      :class="{ active: activeTab === item.pagePath }"
      @click="switchTab(item.pagePath)"
    >
      <image
        class="tab-icon"
        :src="activeTab === item.pagePath ? item.selectedIconPath : item.iconPath"
      />
    </view>
  </view>
</template>

<script setup>
import { ref } from 'vue'
import { onShow } from '@dcloudio/uni-app'

const activeTab = ref('') // 当前选中 tab 的路径

const tabList = [
  {
    pagePath: '/pages/index/index',
    iconPath: '/static/images/home.png', // 未选中图标
    selectedIconPath: '/static/images/homefill2.png' // 选中图标
  },
  {
    pagePath: '/pages/previewList/previewList',
    iconPath: '/static/images/worker.png',
    selectedIconPath: '/static/images/workerfill.png'
  },
  {
    pagePath: '/pages/chatList/chatList',
    iconPath: '/static/images/message.png',
    selectedIconPath: '/static/images/messagefill.png'
  },
  {
    pagePath: '/pages/profile/profile',
    iconPath: '/static/images/我的.png',
    selectedIconPath: '/static/images/我的2.png'
  }
]

// 切换 tab
const switchTab = (path) => {
  uni.switchTab({ url: path })
  activeTab.value = path // 更新当前选中 tab
}

// 页面显示时更新 activeTab
onShow(() => {
  const pages = getCurrentPages()
  const currentPath = '/' + pages[pages.length - 1].route
  activeTab.value = currentPath // 设置当前页面路径
})
</script>

<style scoped>
.custom-tab-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 120rpx; /* 调整高度 */
  background-color: #ffffff; /* 背景色改为纯白 */
  display: flex;
  border-top: 1px solid #e5e5e5;
  z-index: 999;
}

.tab-item {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab-icon {
  width: 50rpx;
  height: 50rpx;
  transition: transform 0.3s ease; /* 平滑过渡效果 */
}

.tab-item.active .tab-icon {
  transform: scale(1.1); /* 选中时放大图标 */
}
</style>